import * as React from 'react';
import Link from '@mui/material/Link';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Title from './Title';
import { PinData } from './Dashboard';

function preventDefault(event: React.MouseEvent) {
  event.preventDefault();
}

export default function Orders({ rows }: {
  rows: PinData[]
}) {
  return (
    <React.Fragment>
      <Title>GPIO Pin State</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Pin</TableCell>
            <TableCell>I/O</TableCell>
            <TableCell>Time</TableCell>
            <TableCell align="right">Last Value</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(({ id, time, name, state, value }) => (
            <TableRow key={id}>
              <TableCell>{name}</TableCell>
              <TableCell>{state}</TableCell>
              <TableCell>{`${time.getHours()}:${time.getMinutes()}`}</TableCell>
              <TableCell align="right">{value || '—'}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </React.Fragment>
  );
}
