function Copyright(props: any) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const drawerWidth: number = 72;

interface AppBarProps extends MuiAppBarProps {
}

const AppBar = styled(MuiAppBar, {})<AppBarProps>(({ theme }) => ({
  zIndex: theme.zIndex.drawer + 1,
  marginLeft: drawerWidth,
  width: `calc(100% - ${drawerWidth}px)`,
}));

const Drawer = styled(MuiDrawer, {})(
  ({ theme }) => ({
    '& .MuiDrawer-paper': {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      boxSizing: 'border-box',
      overflowX: 'hidden',
      [theme.breakpoints.up('sm')]: {
        width: theme.spacing(9),
      },
    },
  }),
);

const mdTheme = createTheme();

export enum PinState {
  IN = 'in',
  OUT = 'out',
}

export interface PinData {
  id: string,
  time: Date,
  name: string,
  state: PinState,
  value: number | null,
}

const time = new Date(2021, 11, 20, 8, 0, 0);

const data = [{
  id: '1',
  time: time,
  name: 'Pin 1',
  state: PinState.IN,
  value: 10,
}, {
  id: '2',
  time: new Date(time.getTime() + 30 * 60 * 1000),
  name: 'Pin 2',
  state: PinState.IN,
  value: 80,
}, {
  id: '3',
  time: new Date(time.getTime() + 60 * 60 * 1000),
  name: 'Pin 3',
  state: PinState.IN,
  value: 340,
}, {
  id: '4',
  time: new Date(time.getTime() + 900 * 60 * 1000),
  name: 'Pin 4',
  state: PinState.OUT,
  value: 80,
}, {
  id: '5',
  time: new Date(time.getTime() + 900 * 60 * 1000),
  name: 'Pin 5',
  state: PinState.OUT,
  value: 35,
}]

export interface OpenViews {
  pins: false,
  data: true
}

